@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{padding:0;margin:0;font-family:'Roboto', sans-serif}html{margin:0 1%;color:#333;background:#cdc url("https://i.imgur.com/LtdNdBp.jpg") repeat top left}

header{box-shadow:0 0 10px 3px rgba(0,0,0,0.3);font-size:1rem;transition:all .8s linear;padding-bottom:11%}.header_logo{float:left;padding:3%;width:20%}.header_logo h1{font-size:3.5em;color:#f48024;text-shadow:0px 0px 5px #0f0600ab}.header_logo .nav_h2{text-align:center;font-size:1.8em}.header_logo .nav_img{margin-top:5px}.nav_banner{background-color:rgba(4,4,68,0.94902);height:46px;display:flex;flex-direction:row;flex-wrap:wrap;align-items:center}.nav_banner .nav_banner-link{color:white;margin:0 3%;font-weight:700;text-decoration:none}.nav_banner .nav_banner-link:hover{color:#f48024}nav{float:right;margin:5%}.all_navbar_links{margin:0;padding:0;list-style:none;float:left}.all_navbar_links .navbar_item{cursor:pointer;float:left;margin:0 0 0 32px;transition:all .2s ease}.all_navbar_links .navbar_item:hover{transform:scale(1.1)}a.navlink_normal,a.navlink-is_active{font-size:1.5em;font-weight:700;text-decoration:none;color:rgba(4,4,68,0.94902)}a.navlink_normal:hover,a.navlink-is_active:hover{color:#f48024}a.navlink_normal{border:none}a.navlink-is_active{border-bottom:2px solid navy}

main{-webkit-animation:fade-in 1s ease-out both 0.3s;animation:fade-in 1s ease-out both 0.3s}@-webkit-keyframes fade-in{from{opacity:0%}to{opacity:100%}}@keyframes fade-in{from{opacity:0%}to{opacity:100%}}.about_banner{width:90%;height:70vh;margin:2em auto;position:relative;border-bottom:1px solid #000;padding-bottom:1%}.about_banner figure{position:absolute;width:100%;height:70vh;opacity:0}.about_banner figure:nth-child(1){-webkit-animation:xfade 16s 12s infinite;animation:xfade 16s 12s infinite}.about_banner figure:nth-child(2){-webkit-animation:xfade 16s 8s infinite;animation:xfade 16s 8s infinite}.about_banner figure:nth-child(3){-webkit-animation:xfade 16s 4s infinite;animation:xfade 16s 4s infinite}.about_banner figure:nth-child(4){-webkit-animation:xfade 16s 0s infinite;animation:xfade 16s 0s infinite}.about_banner figure .about_img{width:100%;height:70vh;background-size:cover;background-attachment:fixed;background-position:50% 50%;background-repeat:no-repeat}.about_banner figure figcaption{position:absolute;bottom:0;visibility:hidden;color:#fff;background:rgba(0,0,0,0.3);font-size:.8rem;padding:5px 10px;opacity:0;transition:opacity .5s}.about_banner figure figcaption:hover figure figcaption{visibility:visible;transition:opacity .5s;opacity:1}@-webkit-keyframes xfade{0%{opacity:0}25%{opacity:1}50%{opacity:0}100%{opacity:0}}@keyframes xfade{0%{opacity:0}25%{opacity:1}50%{opacity:0}100%{opacity:0}}.section_about-why,.section_about-history,.section_about-mission{height:400px}.about_h2,.about_h2-why{font-size:2.2em;text-align:center;margin-bottom:1%}.about_h2-underline{background-color:orange;width:25%;margin:0 37.5% 2%;height:1%}.about_paragraph,.testimonials_description{font-size:1.5em;text-align:center}figure{margin:0;padding:0}.btn{background:orange}.testimonial_content{font-size:1.4em;margin-top:5%;line-height:1.2;color:rgba(4,4,68,0.94902);font-weight:700;padding:0 5% 0 5%}.aside_testimonial{float:right;position:relative;right:14%;margin-top:6%;width:400px}.section_about-testimonials{overflow:hidden;width:460px;left:12em;padding:1em 0;position:relative;text-align:center;border-radius:3px;box-shadow:inset 0 0 1.5em 0 #000;background-color:#ffa5009e}.testimonials_description{margin-top:5%}.about_testimonials{width:1760px;height:400px;position:relative}.about_testimonial-underline{background-color:orange;width:60%;margin:0 19.5% 2%;height:2.5%;position:absolute}.testimonial{float:left;width:400px;position:relative;padding:0 20px;height:100%}.input{position:absolute;clip:rect(0, 0, 0, 0)}.btn{padding:0.3em;cursor:pointer;text-align:center}.btn:hover{color:#fff;text-shadow:0 0 0.1em #eee}.btn-inside{position:absolute;box-shadow:0 0 0.2em #000;border-radius:50%;font-size:2em;font-family:arial;height:1em;width:1em;line-height:0.9;top:180px}.btn-outside{padding:0 1em;margin-top:1em}.next{left:13em}.prev{left:-0.6em}.btn-inside{display:none}#go1:checked ~ .about_testimonials #p1 .btn-inside,#go2:checked ~ .about_testimonials #p2 .btn-inside,#go3:checked ~ .about_testimonials #p3 .btn-inside,#go4:checked ~ .about_testimonials #p4 .btn-inside{display:inline-block}#go1:checked ~ .go1,#go2:checked ~ .go2,#go3:checked ~ .go3,#go4:checked ~ .go4{box-shadow:inset 0 0 1em rgba(255,255,255,0.5)}.btn,.about_testimonials{transition:0.4s}#go1:checked ~ .about_testimonials{transform:translate3d(0, 0, 0)}#go2:checked ~ .about_testimonials{transform:translate3d(-440px, 0, 0)}#go3:checked ~ .about_testimonials{transform:translate3d(-880px, 0, 0)}#go4:checked ~ .about_testimonials{transform:translate3d(-1320px, 0, 0)}

.portfolio_banner{width:90%;height:70vh;margin:2em auto;position:relative;border-bottom:1px solid #000;padding-bottom:1%}.portfolio_img{width:100%;height:70vh;background-size:cover;background-attachment:fixed;background-position:50% 50%;background-repeat:no-repeat}.portfolio_img .portfolio_banner-h2{position:absolute;font-size:4em;color:white;top:18%;left:14%;text-shadow:5px 4px 6px #000000b5}.portfolio_description-paragraph{color:rgba(4,4,68,0.94902);font-size:1.4em;line-height:1.7;text-align:center;font-weight:500;margin:0 5%;padding-bottom:1%}.portfolio_navigation-bar{list-style:none;padding:25px 0px;margin-bottom:40px;margin-left:auto;margin-right:auto;text-align:center;background-color:#f48024}.portfolio_nav-li{display:inline-block;letter-spacing:0.1em;margin:0 15px;font-size:1.3em;font-weight:600;transition:all 0.3s ease 0s}.portfolio_nav-li:hover{cursor:pointer;transform:scale(1.1)}a.portfolio_link-normal,a.link-is_active{color:rgba(4,4,68,0.94902);text-decoration:none}a.portfolio_link-normal:hover,a.link-is_active:hover{border-bottom:1px solid rgba(4,4,68,0.94902)}a.portfolio_link-normal{border:none;background-color:#f48024}a.link-is_active{color:#f48024;background-color:rgba(4,4,68,0.94902);border-bottom:1px solid pink}.portfolio_main{display:inline-flex;margin-right:auto;flex-wrap:wrap;-webkit-animation:bounceIn 2s;animation:bounceIn 2s}@-webkit-keyframes bounceIn{0%{transform:scale(0.1);opacity:0}60%{transform:scale(1.1);opacity:1}100%{transform:scale(1)}}@keyframes bounceIn{0%{transform:scale(0.1);opacity:0}60%{transform:scale(1.1);opacity:1}100%{transform:scale(1)}}@-webkit-keyframes fade-in{from{opacity:0%}to{opacity:100%}}@keyframes fade-in{from{opacity:0%}to{opacity:100%}}

.section_services{-webkit-animation:fade-in 1s ease-out both 0.3s;animation:fade-in 1s ease-out both 0.3s}.services_banner{width:90%;height:70vh;margin:2em auto;position:relative;border-bottom:1px solid #000;padding-bottom:1%}.services_img{width:100%;height:70vh;background-size:cover;background-attachment:fixed;background-position:50% 50%;background-repeat:no-repeat}.services_img .services_banner-h2{position:absolute;font-size:4em;color:white;top:18%;left:14%;text-shadow:5px 4px 6px #000000b5}.services_body{margin-right:auto;margin-left:auto;padding-left:15px;padding-right:15px}.services_article{margin:0 auto 5% auto;padding-left:15px;padding-right:15px;display:flex}.services_img-holder-1,.services_img-holder-2,.services_img-holder-3,.services_img-holder-4{float:left}.services_image-1,.services_image-2,.services_image-3,.services_image-4{max-width:50vw;box-shadow:9px 8px 12px 0px #00000085;border-radius:3px}.services_img-holder-1{float:left}.services_img-holder-1 .services_image-1{float:right}.services_h3{font-size:1.6em;margin-bottom:1%;text-transform:uppercase;color:#f48024;letter-spacing:2px}.services_description-holder-1,.services_description-holder-2,.services_description-holder-3,.services_description-holder-4{padding:5% 0 0 5%}.services_paragraph{font-size:1.4em;line-height:1.4;font-weight:500;color:rgba(4,4,68,0.94902)}@-webkit-keyframes fadeIn{from{opacity:0%}to{opacity:100%}}@keyframes fadeIn{from{opacity:0%}to{opacity:100%}}

.section_contact{-webkit-animation:fade-in 1s ease-out both 0.3s;animation:fade-in 1s ease-out both 0.3s}.contact_banner{width:90%;height:70vh;margin:2em auto;position:relative;border-bottom:1px solid #000;padding-bottom:1%}.contact_img{width:100%;height:70vh;background-size:cover;background-attachment:fixed;background-position:50% 50%;background-repeat:no-repeat}.contact_img .contact_banner-h2{position:absolute;font-size:4em;color:white;top:18%;left:14%;text-shadow:5px 4px 6px #000000b5}.contact_body{display:flex;flex-direction:row;justify-content:space-evenly;flex-wrap:wrap;margin-bottom:7%}.location_div,.email_div,.phone_div{display:flex;flex-direction:column;flex-wrap:wrap;padding:1% 3% 1% 0;align-items:center}.location_div,.email_div{border-right:1px solid rgba(4,4,68,0.94902)}.contact_h3{color:#f48024;font-size:1.7em;margin-bottom:12px;letter-spacing:2px}.contact_link{text-decoration:none;transition:all .2s ease;font-size:1.4em;color:rgba(4,4,68,0.94902);font-weight:500}.contact_link:hover{cursor:pointer;transform:scale(1.1)}.contact_address-paragraph{font-size:1.4em;line-height:1.4;text-align:center;font-weight:500;color:rgba(4,4,68,0.94902)}.contact_socialMedia{display:flex;flex-direction:column;flex-wrap:wrap;justify-content:center;align-items:center}.contact_socialMedia .social_h3{font-size:2em;margin-bottom:1.5%}.contact_socialMedia .contact_socialMedia-img{color:#f48024;font-size:3em;margin:0 1%}.contact_socialMedia .contact_socialMedia-img:hover{cursor:pointer}.contact_media-holder{display:flex;flex-direction:row}@-webkit-keyframes fadeIn{from{opacity:0%}to{opacity:100%}}@keyframes fadeIn{from{opacity:0%}to{opacity:100%}}

footer{background-color:rgba(4,4,68,0.94902);padding:1%;display:flex;flex-direction:row;border-radius:5px;border-radius:5px;justify-content:flex-end;margin-top:5%}.logo-img{width:25%;margin-right:5%}.footer_socialMedia-holder{margin:0 5%;align-self:center}.footer_links-holder{display:flex;flex-direction:column;justify-content:center;padding:1% 9%;border-right:1px solid #fff;border-left:1px solid #fff}.footer_link{color:#fff;font-size:1.5em;font-weight:700;padding:1.5%;width:150px;margin:2%;text-decoration:none}.footer_link:hover{cursor:pointer;color:#f48024}.footer_socialMedia-img{color:#fff;font-size:3em;margin:0 1%}.footer_socialMedia-img:hover{cursor:pointer}.footer_quote{color:#fff;font-size:2em;margin-bottom:12%}



.portfolio_main-projects{display:table;width:auto;margin-left:auto;margin-right:auto}.project{display:table-cell;float:left;width:325px;height:210px;margin-right:20px;margin-bottom:20px;position:relative;border:2px solid #005798;cursor:pointer;overflow:hidden}.project:hover .reveal-top{top:-100%}.reveal-content{box-shadow:1px 1px 3px rgba(0,0,0,0.3) inset;color:#000;transition:left 0.4 ease}.project_reveal-img{background-size:cover;padding:none;float:left;width:325px;height:210px}.reveal-top{background:#040444c9;position:absolute;width:100%;height:100%;top:0;left:0;transition:all 0.6s ease}.caption{background:#0000008f;color:#fff}.project_reveal-h4-name,.project_reveal-h4-location{font-size:1.4em;color:white;position:absolute;width:100%;height:100%;text-align:center;padding:3%}.project_reveal-h4-location{top:35px}

