@import '../SCSS/variables';
@import '../SCSS/media-queries';

header {
    -webkit-box-shadow: 0 0 10px 3px rgba(0,0,0,.3);
    box-shadow: 0 0 10px 3px rgba(0,0,0,.3);
    font-size: 1rem;
    -webkit-transition: all .8s linear;
    transition: all .8s linear;
    padding-bottom: 11%;
}

.header_logo {
    float: left;
    padding: 3%;
    width: 20%;
    h1 {
        font-size: 3.5em;
        color: #f48024;
        text-shadow: 0px 0px 5px #0f0600ab;
    }
    .nav_h2 {
        text-align: center;
        font-size: 1.8em;
    }
    .nav_img {
        margin-top: 5px;
    }
}

.nav_banner {
    background-color: $main_color;
    height: 46px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    .nav_banner-link {
        color: white;
        margin: 0 3%;
        font-weight: 700;
        text-decoration: none;
          &:hover {
              color: $accent_color;
          }
    }
}

nav {
    float: right;
    margin: 5%;
}

.all_navbar_links {
    margin: 0;
    padding: 0;
    list-style: none;
    float: left;
    .navbar_item {
        cursor: pointer;
        float: left;
        margin: 0 0 0 32px;
        transition: all .2s ease;
        &:hover {
            transform: scale(1.1);
        }
    }
}

a.navlink_normal,
a.navlink-is_active {
    font-size: 1.5em;
    font-weight: 700;
    text-decoration: none;
    color: $main_color;
    &:hover {
        color: $accent_color;;
    }
}

a.navlink_normal {
    border: none;
}

a.navlink-is_active {
    border-bottom: 2px solid navy;
}